<template>
  <v-tabs
    centered
    color="white"
    icons-and-text
  >
    <v-tabs-slider color="black"></v-tabs-slider>


    <v-tab href="#tab-0">
      Users
      <v-icon>account_circle</v-icon>
    </v-tab>

    <v-tab href="#tab-1">
      Rooms
      <v-icon>meeting_room</v-icon>
    </v-tab>

    <v-tab-item
      v-for="item in tabs"
      :key="item.Title"
      :value="'tab-' + item.Id"
    >
      <v-card flat>
        <v-card-text>
            <div v-if="item.Id === 0">
                <Users/>
            </div>
            <div v-if="item.Id === 1">
                <Rooms/>
            </div>            
        </v-card-text>
      </v-card>
    </v-tab-item>
  </v-tabs>
</template>

<script>
import Users from './Users';
import Rooms from './Rooms';
  export default {
        components: {
    Users,Rooms
  },
    data () {
      return {
        tabs:[
            {Id:0,Title:'Users',Component:'<Users/>'},
            {Id:1,Title:'Rooms',Component:'<Rooms/>'}
        ]
      }
    }
  }
</script>